@import url(https://fonts.googleapis.com/css?family=Exo+2:200i,300|Raleway:300,400);

:root {
  /* Set neon color */
  --neon-text-color: rgb(255, 0, 242);
  --neon-border-color: #08f;
}

h1 {
  font-family: 'Exo 2', sans-serif;
  font-size: 26px;
  line-height:1.2em;
  font-weight: 200;
  font-style: italic;
  color: #fff;
  padding: 10px;
  border: 0.15rem solid #fff;
  border-radius: 1rem;
  width: 230px;
  margin: 10px auto;
  text-transform: uppercase;
  animation: flicker 1.5s infinite alternate;     
}

h1::-moz-selection {
  background-color: var(--neon-border-color);
  color: var(--neon-text-color);
}

h1::selection {
  background-color: var(--neon-border-color);
  color: var(--neon-text-color);
}

h1:focus {
  outline: none;
}

/* Animate neon flicker */
@keyframes flicker {
  0%, 19%, 21%, 23%, 25%, 54%, 56%, 100% {
    text-shadow:
        -0.2rem -0.2rem 1rem #fff,
        0.2rem 0.2rem 1rem #fff,
        0 0 2rem var(--neon-text-color),
        0 0 4rem var(--neon-text-color),
        0 0 6rem var(--neon-text-color),
        0 0 8rem var(--neon-text-color),
        0 0 0rem var(--neon-text-color); 
    box-shadow:
        0 0 .5rem #fff,
        inset 0 0 .5rem #fff,
        0 0 2rem var(--neon-border-color),
        inset 0 0 2rem var(--neon-border-color),
        0 0 1rem var(--neon-border-color),
        inset 0 0 2rem var(--neon-border-color);        
  }
  20% {        
      text-shadow: none;
      box-shadow: none;
  }    
}

@-webkit-keyframes flicker {
  0%, 19%, 21%, 23%, 25%, 54%, 56%, 100% {
    text-shadow:
        -0.2rem -0.2rem 1rem #fff,
        0.2rem 0.2rem 1rem #fff,
        0 0 2rem var(--neon-text-color),
        0 0 4rem var(--neon-text-color),
        0 0 6rem var(--neon-text-color),
        0 0 8rem var(--neon-text-color),
        0 0 0rem var(--neon-text-color);  
    box-shadow:
        0 0 .5rem #fff,
        inset 0 0 .5rem #fff,
        0 0 2rem var(--neon-border-color),
        inset 0 0 2rem var(--neon-border-color),
        0 0 1rem var(--neon-border-color),
        inset 0 0 2rem var(--neon-border-color);        
  }
  20% {        
      text-shadow: none;
      box-shadow: none;
  }    
}


/* message */
#msg {
  font-size: 16px;
  margin: 20px 0;
  font-style: italic;
}
.red {
  animation: 0.5s color-red;
  color: #f74848;
}
.pink {
  animation: 0.5s color-pink;
  color: #e890d9;
}
.white {
  animation: 0.5s color-white;
  color: white;
}
@keyframes color-red {
  from {opacity: 0;}
  to {color: #f74848;
    opacity: 1;}
}
@-webkit-keyframes color-red {
  from {opacity: 0;}
  to {color: #f74848;
    opacity: 1;}
}
@-webkit-keyframes color-pink {
  from {opacity: 0;}
  to {color: #e890d9;
    opacity: 1;}
}
@keyframes color-pink {
  from {opacity: 0;}
  to {color: #e890d9;
    opacity: 1;}
}
@-webkit-keyframes color-white {
  from {opacity: 0;}
  to {opacity: 1;}
}
@keyframes color-white {
  from {opacity: 0;}
  to {opacity: 1;}
}

/* score */
.score { 
  margin: 0 10px 10px;
  border: 1.5px solid #c8caef;
  border-radius: 8px;
  padding: 6px 12px;
}

/* modal-btn */
body {
  position: relative;
}
.modal-btn {
  position: fixed;
  top: 10px;
  right: 10px;
}

/* modal */
.modal-btn {
  color: #c8caef;
  font-size: 20px;
  background: none;
  border: none;
}
.modal-content {
  color: #dcadc6;
  font-size: 16px;
  font-style: italic;
  background: #020f27;
  text-align: left;
  border: 1px solid #dcadc6;
  border-radius: 15px;
}

.modal-header {
  border-bottom: none;
}
.modal-header button {
  margin-left: 0;
}
.how-to-use {
  text-align: center;
  width: 95%;
}
.how-to-use > p {
  font-weight: 400;
  margin-bottom: 0;
  font-size: 1.2em;
}

#my-modal-footer {
  border-top: none;
  justify-content: space-between;
}
#special-thanks {
  margin: 0;
  line-height: 1;
}
#special-thanks a {
  color: #dcadc6;
  text-decoration: none; 
  font-size: 0.7em;
}
.fas {
  font-size: 0.8em;
}
.got-btn {
  color: black;
  background: #dcadc6;
  width: 40%;
  max-width: 80px;
}
.got-btn:hover {
  background: #ce78a6;
}
.close-btn {
  color: #dcadc6;
}

@media screen and (min-width: 520px) {
  #msg {
    font-size: 20px;
  }
  .modal-content {
    font-size: 20px;
  }
}