@import url(https://fonts.googleapis.com/css?family=Exo+2:200i,300|Raleway:300,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  /* color: #ce78a6; */
  color: white;
  background-color: #0b1f44;
  text-align: center;
  min-height: 100vh;

  font-family: 'Raleway', sans-serif;
  font-weight: 100;
}
.card-container {
  margin: 20px auto;
  width: 100vw;
  max-width: 400px;
}

.shake {
  -webkit-animation: shake 0.08s 5 linear alternate;
  animation: shake 0.08s 5 linear alternate;
}
@-webkit-keyframes shake {
  0% {-webkit-transform: rotate(-3deg);}
  100% {-webkit-transform: rotate(3deg);}
}
@keyframes shake {
  0% {-webkit-transform: rotate(-3deg);transform: rotate(-3deg);}
  100% {-webkit-transform: rotate(3deg);transform: rotate(3deg);}
}


@media screen and (min-width: 550px) {
  .card-container {
    width: 84vw;
    max-width: 700px;
  }
}



.image-card {
  width: 100px;
  display: inline-block;
  margin: 3px;
  height: 95px;
}

.image-card > img {
  width: 100%;
  border: 10px solid rgba(0, 0, 0, 0.4);
  border-radius: 15px;
  height: 100%;
  object-fit: cover;
}

.image-card > img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  border: 7px solid black;
}
@media screen and (max-width: 767px) {
  .image-card > img:hover{
    -webkit-transform: none;
            transform: none;
    border: 10px solid rgba(0, 0, 0, 0.4);
  }
}

@media screen and (min-width: 550px) {
  .image-card {
    width: 20vw;
    max-width: 140px;
    height: 18vw;
    max-height: 133px;
  }
}
:root {
  /* Set neon color */
  --neon-text-color: rgb(255, 0, 242);
  --neon-border-color: #08f;
}

h1 {
  font-family: 'Exo 2', sans-serif;
  font-size: 26px;
  line-height:1.2em;
  font-weight: 200;
  font-style: italic;
  color: #fff;
  padding: 10px;
  border: 0.15rem solid #fff;
  border-radius: 1rem;
  width: 230px;
  margin: 10px auto;
  text-transform: uppercase;
  -webkit-animation: flicker 1.5s infinite alternate;
          animation: flicker 1.5s infinite alternate;     
}

h1::-moz-selection {
  background-color: #08f;
  background-color: var(--neon-border-color);
  color: rgb(255, 0, 242);
  color: var(--neon-text-color);
}

h1::selection {
  background-color: #08f;
  background-color: var(--neon-border-color);
  color: rgb(255, 0, 242);
  color: var(--neon-text-color);
}

h1:focus {
  outline: none;
}

/* Animate neon flicker */
@keyframes flicker {
  0%, 19%, 21%, 23%, 25%, 54%, 56%, 100% {
    text-shadow:
        -0.2rem -0.2rem 1rem #fff,
        0.2rem 0.2rem 1rem #fff,
        0 0 2rem rgb(255, 0, 242),
        0 0 4rem rgb(255, 0, 242),
        0 0 6rem rgb(255, 0, 242),
        0 0 8rem rgb(255, 0, 242),
        0 0 0rem rgb(255, 0, 242);
    text-shadow:
        -0.2rem -0.2rem 1rem #fff,
        0.2rem 0.2rem 1rem #fff,
        0 0 2rem var(--neon-text-color),
        0 0 4rem var(--neon-text-color),
        0 0 6rem var(--neon-text-color),
        0 0 8rem var(--neon-text-color),
        0 0 0rem var(--neon-text-color); 
    box-shadow:
        0 0 .5rem #fff,
        inset 0 0 .5rem #fff,
        0 0 2rem #08f,
        inset 0 0 2rem #08f,
        0 0 1rem #08f,
        inset 0 0 2rem #08f; 
    box-shadow:
        0 0 .5rem #fff,
        inset 0 0 .5rem #fff,
        0 0 2rem var(--neon-border-color),
        inset 0 0 2rem var(--neon-border-color),
        0 0 1rem var(--neon-border-color),
        inset 0 0 2rem var(--neon-border-color);        
  }
  20% {        
      text-shadow: none;
      box-shadow: none;
  }    
}

@-webkit-keyframes flicker {
  0%, 19%, 21%, 23%, 25%, 54%, 56%, 100% {
    text-shadow:
        -0.2rem -0.2rem 1rem #fff,
        0.2rem 0.2rem 1rem #fff,
        0 0 2rem rgb(255, 0, 242),
        0 0 4rem rgb(255, 0, 242),
        0 0 6rem rgb(255, 0, 242),
        0 0 8rem rgb(255, 0, 242),
        0 0 0rem rgb(255, 0, 242);
    text-shadow:
        -0.2rem -0.2rem 1rem #fff,
        0.2rem 0.2rem 1rem #fff,
        0 0 2rem var(--neon-text-color),
        0 0 4rem var(--neon-text-color),
        0 0 6rem var(--neon-text-color),
        0 0 8rem var(--neon-text-color),
        0 0 0rem var(--neon-text-color);  
    box-shadow:
        0 0 .5rem #fff,
        inset 0 0 .5rem #fff,
        0 0 2rem #08f,
        inset 0 0 2rem #08f,
        0 0 1rem #08f,
        inset 0 0 2rem #08f;  
    box-shadow:
        0 0 .5rem #fff,
        inset 0 0 .5rem #fff,
        0 0 2rem var(--neon-border-color),
        inset 0 0 2rem var(--neon-border-color),
        0 0 1rem var(--neon-border-color),
        inset 0 0 2rem var(--neon-border-color);        
  }
  20% {        
      text-shadow: none;
      box-shadow: none;
  }    
}


/* message */
#msg {
  font-size: 16px;
  margin: 20px 0;
  font-style: italic;
}
.red {
  -webkit-animation: 0.5s color-red;
          animation: 0.5s color-red;
  color: #f74848;
}
.pink {
  -webkit-animation: 0.5s color-pink;
          animation: 0.5s color-pink;
  color: #e890d9;
}
.white {
  -webkit-animation: 0.5s color-white;
          animation: 0.5s color-white;
  color: white;
}
@keyframes color-red {
  from {opacity: 0;}
  to {color: #f74848;
    opacity: 1;}
}
@-webkit-keyframes color-red {
  from {opacity: 0;}
  to {color: #f74848;
    opacity: 1;}
}
@-webkit-keyframes color-pink {
  from {opacity: 0;}
  to {color: #e890d9;
    opacity: 1;}
}
@keyframes color-pink {
  from {opacity: 0;}
  to {color: #e890d9;
    opacity: 1;}
}
@-webkit-keyframes color-white {
  from {opacity: 0;}
  to {opacity: 1;}
}
@keyframes color-white {
  from {opacity: 0;}
  to {opacity: 1;}
}

/* score */
.score { 
  margin: 0 10px 10px;
  border: 1.5px solid #c8caef;
  border-radius: 8px;
  padding: 6px 12px;
}

/* modal-btn */
body {
  position: relative;
}
.modal-btn {
  position: fixed;
  top: 10px;
  right: 10px;
}

/* modal */
.modal-btn {
  color: #c8caef;
  font-size: 20px;
  background: none;
  border: none;
}
.modal-content {
  color: #dcadc6;
  font-size: 16px;
  font-style: italic;
  background: #020f27;
  text-align: left;
  border: 1px solid #dcadc6;
  border-radius: 15px;
}

.modal-header {
  border-bottom: none;
}
.modal-header button {
  margin-left: 0;
}
.how-to-use {
  text-align: center;
  width: 95%;
}
.how-to-use > p {
  font-weight: 400;
  margin-bottom: 0;
  font-size: 1.2em;
}

#my-modal-footer {
  border-top: none;
  justify-content: space-between;
}
#special-thanks {
  margin: 0;
  line-height: 1;
}
#special-thanks a {
  color: #dcadc6;
  text-decoration: none; 
  font-size: 0.7em;
}
.fas {
  font-size: 0.8em;
}
.got-btn {
  color: black;
  background: #dcadc6;
  width: 40%;
  max-width: 80px;
}
.got-btn:hover {
  background: #ce78a6;
}
.close-btn {
  color: #dcadc6;
}

@media screen and (min-width: 520px) {
  #msg {
    font-size: 20px;
  }
  .modal-content {
    font-size: 20px;
  }
}
